/**
 * @file 多语言工具
 * @author 五灵
 */

import IntlFormat from 'kiwi-intl';
import zhCNLangs from '../langs/zh_CN';
import enUsLangs from '../langs/en_US';

export enum LangEnum {
  zh_CN = 'zh_CN',
  en_US = 'en_US',
  zh_TW = 'zh_TW',
}

/**
 * 获取当前语言的Cookie
 */
export function getCurrentLang(): LangEnum {
  const urlLang = new URL(window.location.href).searchParams.get('lang');
  const cookieLang = (document.cookie.match(/onedesign-locale=([^;$]+)/) || [null, 'zh_CN'])[1];
  const editorLang = (document.cookie.match(/editor-locale=([^;$]+)/) || [null])[1];
  const lang = (editorLang || cookieLang).split(' ')[0];
  if (Object.keys(LangEnum).includes(urlLang)) {
    return urlLang as LangEnum;
  }
  return lang as LangEnum;
}

export function getModalWidth(width: number = 520) {
  return getCurrentLang() === LangEnum.en_US ? width + 200 : width;
}

interface API {
  /**
   * 初始化对应语言
   * @param lang: 对应语言
   * @param metas: 所有语言的语言文件
   */
  init?(lang: string, metas: object): API;
  /**
   * 设置对应语言
   * @param lang: 切换的对应语言
   */
  setLang?(lang: string): void;
  /**
   * 模板填充, 获取对应语言的模板值
   * @param template: 对应语言的模板
   * @param args: 模板的参数
   */
  template?(str: string, args: object): string;
  /**
   * 获取对应语言的值
   * @param name: 对应语言的模板的 Key
   * @param options: 模板的参数
   */
  get(name: string, args?: object): string;
}

type Langs = typeof zhCNLangs & API;

const langs = {
  zh_CN: zhCNLangs,
  en_US: enUsLangs,
};
// 从 Cookie 中取语言值, 默认为 zh_CN
const defaultLang = getCurrentLang();
let curLang;
if (Object.keys(langs).indexOf(defaultLang) > -1) {
  curLang = defaultLang;
} else {
  // 如果没有对应的语言文件, 置为中文
  curLang = 'zh_CN';
}

const intlFormat = (IntlFormat.init(curLang, langs) as any) as Langs;

export default intlFormat;

