/**
 * @file 组件出错时错误展示方案
 * @author 五灵
 * @TODO: 错误提示的设计规范，需要重新设计内容
 * @TODO: 需要支持loading 、error、和 success 状态
 */
import * as React from 'react';
import { Button } from 'antd';
import I18N from '../utils/I18N';

interface StatusBoundaryProps<T> {
  /** 被处理错误的组件 */
  children: React.ReactElement<T>;
  /** 额外的错误处理回调 */
  onError?: (error: Error, info: object) => void;
}

class StatusBoundaryState {
  /** 是否有错误 */
  hasError = false;
  /** 错误堆栈 */
  error = null;
  /** 错误相关信息 */
  info = null;
}
class StatusBoundary<T> extends React.Component<StatusBoundaryProps<T>, StatusBoundaryState> {
  state = new StatusBoundaryState();

  componentDidCatch(error: Error, info: object) {
    this.setState({
      hasError: true,
      error,
      info,
    });

    if (this.props.onError) {
      this.props.onError(error, info);
    }
  }

  /**
   * 处理刷新事件
   */
  handleClickRefresh = () => {
    location.reload();
  };

  /**
   * 渲染错误信息
   * @param error
   * @param info
   */
  renderError(error: Error, info: object) {
    return (
      <div className="oned-error-view">
        <div className="stack-info">
          <h5>错误堆栈</h5>
          {error.stack.split('\n').map((line, index) => (
            <p key={index}>{line}</p>
          ))}
        </div>
        <div className="content-wrapper">
          <h1>{I18N.StatusBoundary.Oops}</h1>
          <p>
            {I18N.StatusBoundary.youCanChoose}
            <Button onClick={this.handleClickRefresh}>{I18N.StatusBoundary.choice.refresh}</Button>
          </p>
        </div>
      </div>
    );
  }

  render() {
    const { children, ...restProps } = this.props;

    if (this.state.hasError) {
      return this.renderError(this.state.error, this.state.info);
    }

    return React.cloneElement(children, restProps);
  }
}

export { StatusBoundary };
